import React from 'react';
import ContentHeader from './content-header'
import '../style/blog.sass'
import { Link } from 'react-router-dom'

const users = [
  { title: 'Lorem Ipsum', date: 'Jan 1, 2020', gender: 'male' },
  { title: 'Another article headline', date: 'Jan 1, 2020', gender: 'male' },
  { title: 'Lorem Ipsum', date: 'Jan 1, 2020', gender: 'male' },
  { title: 'Another article headline', date: 'Jan 1, 2020', gender: 'male' },
  { title: 'Lorem Ipsum', date: 'Jan 1, 2020', gender: 'male' },
  { title: 'Another article headline', date: 'Jan 1, 2020', gender: 'male' },
]

class BlogList extends React.Component {
  render(){
    return <Link className="blog-list-container" to={this.props.blogURL}><li className="blog-list">
    <h2 className="blog-article-headline">{this.props.title}</h2>
    <p className="blog-article-date">{this.props.date}</p>
  </li></Link>
  }
}

class BlogContent extends React.Component {
  render(){
    const usersElements = []
    for (let user of users) {
      usersElements.push( // 循环每个用户，构建 JSX，push 到数组中
        <BlogList title={user.title} date={user.date}></BlogList>
      )
    }

    return <ul className="blog-content">
      {usersElements}
    </ul>
  }
}

class Blog extends React.Component {
  constructor() {
    super();
    this.state = {
      someKey: 'someValue'
    };
  }

  render() {
    return <div className="content" data-aos="fade-in">
      <ContentHeader headline="Blog"></ContentHeader>
      <BlogContent></BlogContent>
    </div>;
  }

  componentDidMount() {
    this.setState({
      someKey: 'otherValue'
    });
    window.scrollTo(0,0)
  }
}

export default Blog;
